@font-face{
    font-family: 'zhanku';
    src:url('../../fonts/ZhanKuKuaiLeTi2016XiuDingBan-1.ttf');
}
.logo{
    /* position: relative; */
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 30px;
    color: rgb(193, 229, 243,0.5);
    letter-spacing: 5px;
    position: fixed;
    top: 20px;
    left: 20px;
    /* pointer-events: none; */
    cursor:default;

}
/* .logo:hover{
    border-bottom: 1px solid yellow;
} */

.underline{
    /* margin-top: 5px; */
    position: absolute;
    bottom: -10px;
    width: 100%;
    height: 2px;

}
.underline::before{
    position: absolute;
    left: 0%;
    content: "all in one";
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 13px;
    color: rgb(193, 229, 243,0.5);
    width: 100%;
    height: 100%;
    background:rgba(169, 221, 241,0);
    transition: 0.5s ease;
    transform: scaleX(0);
}
.logo:hover .underline::before{
    transform: scaleX(1);
}