.oneword{
    color: rgba(197, 193, 193,0.7);
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 60%;
    margin-top: 80px;
    
    padding: 10px 20px;
    transition: 0.5s ease;
}
.oneword:hover{
    color: rgb(197, 193, 193);
    backdrop-filter:blur(15px);
    
}
.oneword:hover .origin{
    color: rgba(197, 193, 193);
    
}
.origin{
    margin-top: 10px;
    color: rgba(197, 193, 193,0.7);
    font-size: 15px;
    transition: 0.5s ease;
}