*{
    padding: 0;
    margin: 0;
    
}
.clock{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    width: 120px;
    height: 50px;
    margin-top: -40px;
    /* background:linear-gradient(135deg,rgb(153, 160, 224),rgb(118, 130, 235)); */
    backdrop-filter: blur(7px);
    border-radius: 10px;
    animation: identifier 4s ease-in-out infinite;
}
.board{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    width: 90%;
    height: 80%;
    /* background-color: rgba(32, 33, 36); */
    backdrop-filter: blur(15px);
    border-radius: 10px;
}
#time{
    box-sizing: content-box;
    font-size: 30px;
    font-weight: bold;
    letter-spacing: 2px;
    color: transparent;
    background:linear-gradient(135deg,#56d5fc,#0063f7,#0c0fa5);
    background-clip: text;
    -webkit-background-clip: text;
}
.clock:hover{
    animation: hover 0.6s ease-in-out 1;
    
}
@keyframes identifier {
    50%{
        filter: hue-rotate(-30deg);
    }
    100%{
        filter: hue-rotate(0deg);
    }
    
}
@keyframes hover {
    25%{
        width: 110px;
        border-radius: 5px;
    }
    50%{
        width: 130px;
        border-radius: 20px;
    }
    75%{
        width: 110px;
        border-radius: 5px;
    }
    100%{
        width: 120px;
        border-radius: 10px;
    }
}