.active{
    color: rgb(193, 229, 243,1);
    cursor: pointer;
}
.music{
    position: fixed;
    top: 10px;
    right: 230px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    height: 40px;
    backdrop-filter: blur(10px);
    padding: 5px 5px;
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 20px;
}

.music_board{
    position: fixed;
    left: calc(50% - 400px) ;
    top: calc(50% - 350px);
    width: 800px;
    height: 700px;
    /* transform: translateX(50%);
    transform: translateY(-50%); */
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    transform: scale(0);
    animation-fill-mode: forwards;
}
.cancel{
    position: absolute;
    top: 0px;
    right: 10px;
    color: rgb(193, 229, 243,0.5);
    font-size: 30px;
    cursor: pointer;
    transition: 0.25s ease;
    
}
.cancel:hover{
    color: rgba(212, 237, 247, 1);
}
