.music_underline{
    /* margin-top: 5px; */
    position: absolute;
    bottom: 10px;
    width: 100%;
    height: 4px;
    /* padding: 0 5px; */

}
.music_underline::before{
    position: absolute;
    left: 0%;
    content: "";
    /* font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 13px; */
    /* color: rgb(193, 229, 243,0.5); */
    width: 100%;
    height: 100%;
    background: rgb(193, 229, 243,1);
    transition: 0.5s ease;
    transform: scaleX(0);
}
.search_music:hover .music_underline::before{
    transform: scaleX(1);
}
.hot_search:hover .music_underline::before{
    transform: scaleX(1);
}
.active > .music_underline::before{
    transform: scaleX(1);
}

.music_list:hover .music_underline::before{
    transform: scaleX(1);
}
.bar>div:hover .music_underline::before{
    transform: scaleX(1);
}
