@keyframes musicin {
    /* 25%{
        transform: scale(1.2);
    } */
    50%{
        transform: scale(1.05);
        border-radius: 10px;
    }
    /* 75%{
        transform: scale(1.2);
    } */
    100%{
        transform: scale(1);
        border-radius: 15px;
    }
}

@keyframes musicout {
    /* 25%{
        transform: scale(1.2);
    } */
    50%{
        transform: scale(1.05);
        border-radius: 10px;
    }
    /* 75%{
        transform: scale(0.4);
