.music_search_box{
    width: 95%;
    height: 95%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* background-color: #fff; */
}
.music_search_result {
    margin-top: 30px;
    width: 95%;
    height: 100%;
    padding: 10px;
    overflow: scroll;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    scroll-behavior: smooth;
    overflow-x: hidden;
    background: none;
}
