*{
    padding: 0;
    margin: 0;
}
.big-box{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 50px;
}
.search-box{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    width: 80px;
    height: 80px;
    /* background: linear-gradient(135deg,rgb(17, 24, 87),rgb(4, 8, 41)); */
    backdrop-filter: blur(15px);
    border-radius: 80px;
    transition: 1s ease;
}
.search-input{
    /* display: none; */
    border: none;
    outline: none;
    background-color: rgb(255, 255, 255,0);
    caret-color: rgb(255, 255, 255);
    color: rgb(255, 255, 255);
    font-size: 24px;
    letter-spacing: normal;
    width: 0px;
    /* margin-left: 15px; */
    height: 60%;
    /* transform: scaleX(0); */
    transition: 1s ease;
    border-radius: 15px;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 80px;
    height: 80px;
    /* background: linear-gradient(135deg,rgb(17, 24, 87),rgb(4, 8, 41)); */
    backdrop-filter: blur(15px);
    border-radius: 80px;

    transition: 1s ease;
    pointer-events: none;
}
.icon{
    width: 60%;
    height: 60%;
    color: white;
}
.icon-bd{
    width: 50%;
    height: 50%;
    color: white;
}
.icon-bili{
    width: 40%;
    height: 40%;
    color: white;
}
.icon-csdn{
    width: 50%;
    height: 50%;
    color: white;
}
.icon-zhihu{
    width: 50%;
    height: 50%;
    color: white;
}
.icon-guge{
    width: 50%;
    height: 50%;
    color: white;
}
.big-box:hover .search-box{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    margin-top: 40px;
    width: 500px;
    height: 80px;
    /* background-color: rgb(17, 24, 87); */
    backdrop-filter: blur(15px);
    border-radius: 80px;
}
.big-box:hover .search-input{
    /* display: block; */
    /* transform: scaleX(1); */
    margin-left: 15px;
    width: 400px;
    
}
.big-box:hover .search-icon{
    width: 70px;
    height: 70px;
    background: rgb(255, 255, 255);
    pointer-events: fill;
}

.select{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    width: 600px;
    height: 80px;
    background-color: rgb(27, 20, 20,0);
}
.select>div:first-child{
    width: 0px;
    height: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: rgb(4, 8, 41);
    
}
/* .big-box:hover .select{
    margin-bottom: 20px;
} */
.big-box:hover .select>div:first-child{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    margin: auto 10px;
    border-radius: 50%;
    
    background-color: white;
    transition: .5s ease;
}
