.history{
    position: fixed;
    top: 10px;
    right: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    height: 40px;
    backdrop-filter: blur(10px);
    padding: 5px 5px;
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 20px;
}

.history_board{
    position: fixed;
    left: calc(50% - 300px) ;
    top: calc(50% - 350px);
    width: 600px;
    height: 700px;
    /* transform: translateX(50%);
    transform: translateY(-50%); */
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    transform: scale(0);
    animation-fill-mode: forwards;
}
.cancel{
    position: absolute;
    top: 0px;
    right: 10px;
    color: rgb(193, 229, 243,0.5);
    font-size: 30px;
    cursor: pointer;
    transition: 0.25s ease;
    
}
.cancel:hover{
    color: rgba(212, 237, 247, 1);
}
.date{
    position: absolute;
    top: 15px;
    left: 30px;
    color: rgb(193, 229, 243,0.5);
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    pointer-events: none;
}
.event_board{
    width: 90%;
    padding: 2px;
    margin: 60px 5%;
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
@keyframes historyin {
    /* 25%{
        transform: scale(1.2);
    } */
    50%{
        transform: scale(1.05);
        border-radius: 10px;
    }
    /* 75%{
        transform: scale(1.2);
    } */
    100%{
        transform: scale(1);
        border-radius: 15px;
    }
}

@keyframes historyout {
    /* 25%{
        transform: scale(1.2);
    } */
    50%{
        transform: scale(1.05);
        border-radius: 10px;
    }
    /* 75%{
        transform: scale(0.4);
    } */
    100%{
        transform: scale(0);
        border-radius: 50%;
    }
}