.event{
    
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-decoration: none;
    border-radius: 5px;
    color: rgb(193, 229, 243,0.7);
    padding: 3px;
    margin: 6px 0;
    transition: 0.5s ease;
}
.event:hover{
    color: rgba(212, 237, 247, 1);
    background-color: rgba(0, 0, 0, 0.2);
}
