.MusicSearchDetail_item{
    overflow: hidden;
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-decoration: none;
    /* border-radius: 5px; */
    color: rgb(193, 229, 243,0.5);
    padding: 3px;
    margin: 10px 0;
    transition: 0.5s ease;
    /* background-color: rgba(128, 128, 128,0.7); */
    backdrop-filter: blur(25px);
    cursor: pointer;
}

.MusicSearchDetail_item:hover{
    color: rgba(212, 237, 247, 1);
    background-color: rgba(187, 187, 187,0.7);
}
