.input-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 80px;
    margin-top: 0px;
    /* background-color: rgba(255, 255, 255, 0.1); */
    /* border-radius: 5px; */
    /* box-shadow: 0px 0px 10px rgba(255, 241, 241, 0.5); */
}
.input{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.input input{
    border: none;
    border-bottom: 1px solid rgb(193, 229, 243,0.5);
    outline: 0;
    background-color: rgba(255, 255, 255, 0.0);
    font-size: 20px;
    caret-color: rgb(193, 229, 243,0.5);
    color: rgb(193, 229, 243,1);
}
label{
    width: 50px;
    height: 50px;
    transition: 0.5s ease;
    position: absolute;
    bottom: -15px;
    font-size: 10px;
    /* pointer-events: none; */
    cursor: pointer;
}
.input-underline{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2px;

}
.input-underline::before{
    position: absolute;
    left: 0%;
    content: "";
    width: 100%;
    height: 100%;
    background:rgb(193, 229, 243,1);
    transition: 0.5s ease;
    transform: scaleX(0);
}
.input input:focus ~ .input-underline::before{
    transform: scaleX(1);
}
.input input:focus ~ label{
    transform: translateY(-20px);
    font-size: 15px;
}