.loading{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:rgb(27, 27, 27) ;
    z-index: 10;
    transition: 0.5s ease-out;
    overflow: hidden;
}
.loading-pic{
    position: absolute;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 30px;
    color: rgb(193, 229, 243,0.5);
    letter-spacing: 5px;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
}
.line{
    /* margin-top: 5px; */
    position: absolute;
    bottom: -10px;
    width: 100%;
    height: 2px;
    overflow: hidden;
}
.line::before{
    position: absolute;
    left: 0%;
    content: "";
    
    width: 20%;
    height: 100%;
    background:rgb(193, 229, 243,0.5);
    transition: 0.5s ease;
    /* transform: scaleX(0); */
    transform: translateX(-50%);
    animation: line 1.5s linear infinite;
}
@keyframes line {
    20%{
        transform: translateX(100%);
    }
    40%{
        transform: translateX(200%);
    }
    60%{
        transform: translateX(300%);
    }
    80%{
        transform: translateX(400%);
    }
    100%{
        transform: translateX(500%);
    }
}