.tab{
    position: fixed;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    overflow: hidden;
    transition: 0.5s ease-in-out;
}
.tab:hover{
    
    height: 250px;
}
.square{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px 5px;
    background-color: rgba(red, green, blue, 0);
    width: 35px;
    height: 35px;
    transition: 0.25s ease-in-out;
}
.icon-more{
    width: 90%;
    height: 90%;
    transition: 0.5s ease-in-out;
    pointer-events: none;
}
.icon-qq{
    width: 90%;
    height: 90%;
    transition: 0.5s ease-in-out;
}
.icon-mail{
    width: 100%;
    height: 100%;
    transition: 0.5s ease-in-out;
}
.icon-fanyi{
    width: 70%;
    height: 70%;
    transition: 0.5s ease-in-out;
}
.square:hover{
    width: 40px;
    height: 40px;
}